<template>
    <div class="losePW">
        <van-nav-bar
            title="找回密码"
            left-text="返回"
            @click-left="back"
            left-arrow
        />
        <div class="box">
            <van-form>
                
                <van-field
                    v-model="userInfo.userEmail"
                    name="email"
                    label="邮箱"
                    placeholder="请输入邮箱"
                />
                <!-- <van-field key="code" v-model="userInfo.testingCode" type="text" label="验证码" placeholder="6位验证码">
                    <template #button>
                    <van-button size="small" type="info" @click="openEmail" :disabled="isButHas" :text="openInfo"></van-button>
                    </template>
                </van-field> -->
               
                <div class="box_email">
                    <van-field
                        v-model="userInfo.testingCode"
                        name="validCode"
                        label="验证码"
                        placeholder="请输入六位验证码"
                    />
                    <div class="email_testing" @click.stop="openEmail" >
                        
                        <van-button size="small" type="info" :text="openInfo" :disabled="isButHas" class="testing_num"/>
                    </div>
                 </div>
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit" @click="onSubmit" :disabled="submBtn">
                    下一步
                    </van-button>
                </div>
            </van-form>
        </div>
    </div>
</template>

<script>
import validForm from '../assets/js/validForm'
    export default {
        data(){
            return {
                userInfo:{
                    userEmail:'',
                    testingCode: ''
                },
                openInfo: '发送验证码',
                isButHas: false,
                saveEmail: '',
                submBtn: true
            }
        },
        methods: {
            openEmail(){
                if (!validForm.validUserForm({email: this.userInfo.userEmail},this)) {
                    return;
                }
                let timeNum=60;

                let runTime=setInterval(() => {
                     timeNum--;
                    if(timeNum<=0){
                        this.isButHas=false;
                        this.openInfo='发送验证码';
                        this.submBtn=true;
                        clearInterval(runTime);
                    }else{
                        this.isButHas=true;
                        this.openInfo=timeNum+'S';
                        this.submBtn=false;
                    }
                }, 1000);

               this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })

                this.axios({
                    method: "POST",
                    url: '/emailValidCode',
                    data:{
                       appkey: this.appkey,
                       email: this.userInfo.userEmail
                    }
                }).then(result=>{
                    this.$toast.clear();
                    
                    this.saveEmail=this.userInfo.userEmail;
                }).catch(err=>{
                    this.$toast.clear();
                    
                })

               
            },
            onSubmit(value){
                
                if (!validForm.validUserForm({email: this.userInfo.userEmail,code: this.userInfo.testingCode},this)) {
                    return;
                }
                if(this.saveEmail != this.userInfo.userEmail){
                    this.$toast('邮箱输入错误!');
                    return;
                }
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })

                this.axios({
                    method: "POST",
                    url: '/checkValidCode',
                    data:{
                       appkey: this.appkey,
                       validCode: this.userInfo.testingCode
                    }
                }).then(result=>{
                    this.$toast.clear();
                    
                    this.$toast(result.data.msg);
                    if(result.data.code=="K001"){
                        this.$router.push({name: 'UpLosePW'});
                    }
                }).catch(err=>{
                    this.$toast.clear();
                    
                })
            },
            back(){
                this.$router.go(-1);
            }
        }
    }
</script>

<style lang="less" scoped>
    .box{
        width: calc(~"100% - 50px");
        padding: 25px;
        .box_email{
            position: relative;
        }
        .email_testing{
            position: absolute;
            height: 44px;
            width: 100px;
            line-height: 44px;
            text-align: center;
            // background-color: skyblue;
            top: 0px;
            right: 0px;
        }
        .testing_num{
            width: 100%;
            height: 100%;
        }
    }
</style>